.historywhole{
    border:1px solid black;
    margin: 10px;
    background-color: rgb(0,0,0,0.50);
    border-radius: 5px;
    margin-left: 10%;
    margin-right: 10%;
}
.historysingle{
    border:solid black;
    border-width: 2px 6px 6px 2px;
    border-radius: 5px;
    margin: 10px;
    font-size: 20px;
    padding: 0px;
    
}
.historysingle:hover{
    background-color: rgb(0,0,0,0.55);
    border-width: 2px 6px 6px 2px;
}